.container {
	width: 1260px;
	margin: 0 auto;
	max-width: 95%;
	max-width: calc(100% - 40px);
}

.button {
	display: flex;
	align-items: center;
	justify-content: center;
	font-family: $gilroy;
	font-weight: 300;
	font-size: 16px;
	line-height: 18px;
	color: $white;
	height: 35px;
	padding: 10px 28px 12px 27px;
	border-radius: 5px;
	background-color: $red;
	cursor: pointer;
	position: relative;

	svg {
		margin-left: 5px;
	}

	&--arrow {
		padding: 10px 41px 9px 23px;
		box-shadow: 0 6px 10px rgba(221, 32, 54, 0.2);

		span {
			position: relative;
			display: block;

			&::before,
			&::after {
				content: '';
				display: block;
				width: 8px;
				height: 2px;
				background-color: $white;
				position: absolute;
				right: -16px;
			}

			&::before {
				top: 68%;
				transform: translate(0, -68%) rotate(-45deg);
			}

			&::after {
				top: 40%;
				transform: translate(0, -44%) rotate(45deg);
			}
		}
	}
}

.input {
	width: calc(100% - 32px);
	background-color: $light-gray;
	border-radius: 5px;
	padding: 12px 16px;

	&::-webkit-input-placeholder {
		opacity: 1;
		transition: opacity 0.3s ease;
	}

	&::-moz-placeholder {
		opacity: 1;
		transition: opacity 0.3s ease;
	}

	&:-moz-placeholder {
		opacity: 1;
		transition: opacity 0.3s ease;
	}

	&:-ms-input-placeholder {
		opacity: 1;
		transition: opacity 0.3s ease;
	}

	&::placeholder {
		font-family: $gilroy;
		font-size: 16px;
		font-weight: 300;
		color: $gray-tag-dark;
		opacity: 1;
		transition: opacity 0.3s ease;
	}

	&:focus::-webkit-input-placeholder {
		opacity: 0;
		transition: opacity 0.3s ease;
	}

	&:focus::-moz-placeholder {
		opacity: 0;
		transition: opacity 0.3s ease;
	}

	&:focus:-moz-placeholder {
		opacity: 0;
		transition: opacity 0.3s ease;
	}

	&:focus:-ms-input-placeholder {
		opacity: 0;
		transition: opacity 0.3s ease;
	}

	&:focus::placeholder {
		opacity: 0;
		transition: opacity 0.3s ease;
	}
}

.breadcrumbs {
	display: flex;
	flex-wrap: wrap;

	li {
		&:not(:last-child) {
			position: relative;
			display: flex;
			align-items: center;
			justify-content: center;

			.breadcrumbs__arrow {
				display: inline-block;
				position: relative;
				width: 10px;
				height: 3px;
				margin: 0 15px;

				&::before,
				&::after {
					content: '';
					display: block;
					width: 8px;
					height: 2px;
					background-color: $gray-95;
					position: absolute;
					left: 0;
				}

				&::before {
					top: 4px;
					transform: rotate(-45deg);
				}

				&::after {
					top: 0;
					transform: rotate(45deg);
				}
			}
		}
	}

	&__link {
		color: $gray-tag-dark;

		color: $red;
		text-decoration: underline;
		&--span {
			color: $gray-tag-dark;
			text-decoration: none;
		}
	}
}
